@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@100;500;700&display=swap);
:root {
  --bg-color:#212529;
  --main-color: #49A0EB;
  --second-color: #E359A4;
}

body {
  background-color: #212529;
  background-color: var(--bg-color);
  color: #E359A4;
  color: var(--second-color);
  font-family: 'IBM Plex Mono', monospace;
  padding: 0;
  margin: 0;
}

h1 {
  color: #E359A4;
  color: var(--second-color);
}

header h1 {
  color: #49A0EB;
  color: var(--main-color);
  font-weight: 800;
}

header p {
  line-height: 1.5em;
}

header {
  display: grid;
  grid-template-columns: 100px 1fr;
  width: 100%;
}

.intro-box {
  background-color: #49A0EB;
  background-color: var(--main-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.intro-box img {
  height: 100px;
}

.bio {
  padding: 1em;
}

p.bio {
  grid-column: 1 / 3;
  padding: 2em;
}

@media (min-width: 768px) {
  header {
    grid-template-columns: 2fr 2fr 1fr;
    padding-top: 5em;
  }

  header h1 {
    font-size: 3em;
  }

  .intro-box img {
    height: 260px;
  }
}
